class Helper {
  riquierdvalidation (data) {
    if (data == null || data === '') {
      return true
    } else {
      return false
    }
  }

  numircvalidation (data) {
    if (isNaN(data)) {
      return true
    } else if (!isNaN(data) || data == null || data === '') {
      return false
    } else {
      return false
    }
  }

  numircriquierdvalidation (data) {
    if (isNaN(data)) {
      return true
    } else if (!isNaN(data) && data != null && data !== '') {
      return false
    } else {
      return true
    }
  }

  validateEmail (email) {
    const re = /^(([^<>()[\]\\.,;:\s@\\"]+(\.[^<>()[\]\\.,;:\s@\\"]+)*)|(\\".+\\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    return !(re.test(email))
  }

  validatePhone (phone) {
    const re = /^[\\+]?[(]?[0-9]{3}[)]?[-\s\\.]?[0-9]{3}[-\s\\.]?[0-9]{5,6}$/im
    return !(re.test(phone))
  }

  todayDate () {
    var today = new Date()
    var date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate()
    return date
  }

  formatDate (dateString, FullDate = true) {
    if (FullDate) {
      const options = { weekday: 'short', year: 'numeric', month: 'numeric', day: 'numeric', hour: 'numeric', minute: 'numeric' }
      return new Date(dateString).toLocaleDateString('en-GB', options)
    } else {
      const options = { year: 'numeric', month: 'numeric', day: 'numeric' }
      return new Date(dateString).toLocaleDateString('en-GB', options)
    }
  }
}

export default new Helper()
