<template>
  <div class="contanier">
    <div class="row" v-if="showInv" >
      <invoicesBuy-Table :CustomerId="currentCustomer" class="flex md12 sm12 xs12"/>
    </div>
    <div class="row" v-if="!showInv" style="justify-content: center;" >
      <va-card :title="$t('tables.labelSupplierChoose')" class="flex md8 sm12 xs12">
        <form @submit.prevent="okCreatefun()">
          <div class="row flex-center">

            <div class="flex md4 sm12 xs12 px-1">
              <va-select
                :label="$t('placeholder.SupplierName')"
                v-model="fKCustomer.valueName"
                required
                searchable
                textBy="name"
                keyBy="id"
                :options="fKCustomer.options"
                :disabled="fKCustomer.valuePhone !== ''"

              />
            </div>
            <div class="flex md4 sm12 xs12 px-1">
              <va-select
                :label="$t('placeholder.SupplierPhone')"
                v-model="fKCustomer.valuePhone"
                required
                searchable
                textBy="phone"
                keyBy="id"
                :options="fKCustomer.options"
                :disabled="fKCustomer.valueName !== ''"
              />
            </div>
          </div>

          <div class="d-flex justify--center mt-3">
            <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t('modal.ok') }} </va-button>
          </div>
        </form>
      </va-card>
    </div>
  </div>
</template>

<script>
import invoicesBuyTable from './invoicesBuy/invoicesBuyTable'
import customerslistService from '../../../services/customers/customerslistService'

export default {
  name: 'invoicesBuy',
  components: {
    invoicesBuyTable,
  },
  data () {
    return {
      showInv: false,
      stopBtn: false,
      fKCustomer: {
        valueName: '',
        valuePhone: '',
        options: [],
      },
      currentCustomer: '',
    }
  },
  created () {
    this.getCustomer()
  },
  methods: {
    getCustomer () {
      const params = {
        Customer_type: 2,
      }
      customerslistService.getAll(params)
        .then(response => {
          this.fKCustomer.options = response.data
        },
        err => {
          console.error(err)
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    okCreatefun () {
      this.stopBtn = true
      if ((this.fKCustomer.valueName !== null && this.fKCustomer.valueName !== undefined && this.fKCustomer.valueName !== '') || (this.fKCustomer.valuePhone !== null && this.fKCustomer.valuePhone !== undefined && this.fKCustomer.valuePhone !== '')) {
        if ((this.fKCustomer.valueName !== null && this.fKCustomer.valueName !== undefined && this.fKCustomer.valueName !== '')) {
          this.currentCustomer = this.fKCustomer.valueName.id
          this.showInv = !this.showInv
        } else {
          this.currentCustomer = this.fKCustomer.valuePhone.id
          this.showInv = !this.showInv
        }
      } else {
        this.stopBtn = false
        this.showToast(this.$t('generic.errorChooseSupplier'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
    },
  },
}

</script>

<style lang="scss">
  .row-equal .flex {
    .va-card {
      height: 100%;
    }
  }

  .dashboard {
    .va-card {
      margin-bottom: 0 !important;
    }
  }

</style>
