<template>
  <div class="grid row">

    <div class="row" style="justify-content: center;" v-if="loading">
      <div class="flex-center spinner-box">
        <SpringSpinner
          :animation-duration="1500"
          :color="this.$themes.primary"
          :size="80"
        >
        </SpringSpinner>
      </div>
    </div>
    <div class="row" v-if="!loading">
      <a :style="floatingbtncomputedStyle()"  @click="showsubmitModdal">
        <i class="fa fa-plus my-float"></i>
      </a>
      <div class="flex xs12 md6 ">
        <va-input
          :value="searchBarCodeTerm"
          :placeholder="$t('tables.searchByBarCode')"
          @input="searchBarcode"
          removable
        >
          <va-icon name="fa fa-search" slot="prepend" />
        </va-input>
        <va-card :title="$t('tables.labelBillItems')">
          <va-data-table
            :fields="fields"
            :data="tableItems"
            class="data-table-infinite-scroll--container"
            no-pagination
          >
            <template slot="marker" slot-scope="props">
              {{props.rowIndex + 1}}
            </template>

            <template slot="actions" slot-scope="props">
              <va-button flat small color="gray" @click="showEditModal(props.rowData)" class="ma-0">
                {{ $t('tables.edit') }}
              </va-button>

              <va-button flat small color="danger" @click="showDeleteModal(props.rowIndex)" class="ma-0">
                {{ $t('tables.delete') }}
              </va-button>
            </template>
          </va-data-table>
          <div class="row " >
            <div class="flex md4 xs12">
              <p class="display-5">{{$t('generic.totalBilWithoutTax')}}</p>
              <p>
                {{totalBilWithoutTax}}
              </p>
            </div>
            <div class="flex md4 xs12">
              <p class="display-5">{{$t('generic.totalTax')}}</p>
              <p>
                {{totalTax}}
              </p>
            </div>
            <div class="flex md4 xs12">
              <p class="display-5">{{$t('generic.totalBilTax')}}</p>
              <p>
                {{totalBilTax}}

              </p>
            </div>

          </div>
        </va-card>
      </div>
      <div class="flex md6 xs12">
        <va-input
          :value="searchNameTerm"
          :placeholder="$t('tables.searchByName')"
          @input="search"
          removable
        >
          <va-icon name="fa fa-search" slot="prepend" />
        </va-input>
        <va-card :title="$t('tables.labelGroups')" class="Card-infinite-scroll--container">
          <div class="row" v-if="(groupsLevels.length >= 1)">
            <div class="d-flex flex xs12 lg12 border-bottom" v-for="(level, index) in groupsLevels" :key="index">
              <div class=" clickable flex xs6 lg4"  v-for="(group, j)  in level" :key="j"  v-on:click="getProductByGroupId(level[j],index)">
                <div
                  class="grid__container flex-center"
                  :style="computedStyle(25, 1)"
                >
                  <div class="ma-2 text-center">

                    <p class="display-5">{{group.name}}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="row" v-else-if="!(groupsLevels.length >= 1)">
            <div class="flex xs12 lg12">
              <div class="textCenter">
                {{$t('tables.noDataAvailable')}}
              </div>
            </div>
          </div>
        </va-card>

        <va-card :title="$t('tables.labelBestSellProduct')" v-if="!showGroupProduct" class="mt-3 Card-infinite-scroll--container">
          <div class="row" v-if="dataTable.bestSellProducts.length >= 1">
            <div class="clickable flex xs6 lg4"  v-for="(product, j)  in dataTable.bestSellProducts" :key="j"  v-on:click="addProduct(dataTable.bestSellProducts[j])">
              <div
                class="grid__container flex-center"
                :style="computedStyle(product.avilableInStock,product.minQtyStock)"
              >
                <div class="mt-2 text-center">

                  <p class="display-5">{{product.name}}</p>
                  <p>{{product.price}}$</p>
                </div>
              </div>
            </div>
          </div>
          <div class="row" v-else-if="!(dataTable.bestSellProducts.length >= 1)">
            <div class="flex xs12 lg12">
              <div class="textCenter">
                {{$t('tables.noDataAvailable')}}
              </div>
            </div>
          </div>
        </va-card>

        <va-card :title="$t('tables.labelProductGroup')" v-if="showGroupProduct" class="mt-3 Card-infinite-scroll--container">
          <va-button
            icon="fa fa-close"
            flat
            slot="actions"
            color="danger"
            class="ml-0"
            @click="ShowBestSellProductAgain()">
          </va-button>
          <div class="row" v-if="groupProduct.length >= 1">
            <div class="clickable flex xs6 lg4"  v-for="(product, j)  in groupProduct" :key="j"  v-on:click="addProduct(groupProduct[j])">
              <div
                class="grid__container flex-center"
                :style="computedStyle(product.avilableInStock,product.minQtyStock)"
              >
                <div class="mt-2 text-center">

                  <p class="display-5">{{product.name}}</p>
                  <p>{{product.price ? product.price  : '---'}}</p>
                </div>
              </div>
            </div>
          </div>
          <div class="row" v-else-if="!(groupProduct.length >= 1)">
            <div class="flex xs12 lg12">
              <div class="textCenter">
                {{$t('tables.noDataAvailable')}}
              </div>
            </div>
          </div>
        </va-card>
        <va-modal v-model="createModalProductPrice" no-outside-dismiss
          size="large"
          :title="$t(modelTitleProductprice)"
          hide-default-actions >
          <form @submit.prevent="editModalProductPrice? okEditfunProductPrice()  :okCreatefunProductPrice()">
            <div class="row flex-center">

              <div class="flex md4 sm12 xs12 px-1">
                <va-select
                  :label="$t('placeholder.uints')"
                  v-model="UintAndPriceByCUstomer.valueUint"
                  :error="riquierdvalidation(UintAndPriceByCUstomer.valueUint)"
                  :error-messages="['error message']"
                  required
                  searchable
                  textBy="uint_Name"
                  keyBy="fk_Uint"
                  :options="UintAndPriceByCUstomer.options"
                />
              </div>
              <div class="flex md4 sm12 xs12 px-1">
                <label class="input-label"> {{$t('placeholder.price')}} </label>
                <va-input v-model="UintAndPriceByCUstomer.valuePrice" type="number" step="0.5" :min="UintAndPriceByCUstomer.valueUint.min_Sale_Price === undefined? 0:UintAndPriceByCUstomer.valueUint.min_Sale_Price " :max="UintAndPriceByCUstomer.valueUint.max_Sale_Price=== undefined? '' :UintAndPriceByCUstomer.valueUint.max_Sale_Price"
                  :error-messages="$t('errorMessage.price')"
                  :placeholder="$t('placeholder.price')" required/>
              </div>
              <div class="flex md4 sm12 xs12 px-1">
                <label class="input-label"> {{$t('placeholder.quantity')}} </label>
                <va-input v-model="UintAndPriceByCUstomer.valueQuantaty" type="number" step="1" :min="UintAndPriceByCUstomer.valueUint.min_Quantity === undefined ? 0 : UintAndPriceByCUstomer.valueUint.min_Quantity" :max="UintAndPriceByCUstomer.valueUint.avilableInStock !== undefined? UintAndPriceByCUstomer.valueUint.avilableInStock :''"
                  :error="numircriquierdvalidation(UintAndPriceByCUstomer.valueQuantaty,UintAndPriceByCUstomer.valueUint.min_Quantity)"
                  :error-messages="$t('errorMessage.Number')"
                  :placeholder="$t('placeholder.quantity')" required/>
              </div>
              <div class="flex md12 sm12 xs12 px-1">
                <p>
                  {{ (UintAndPriceByCUstomer.valueUint.customerPrice > 0 ?$t("placeholder.customerprice") + ':' + UintAndPriceByCUstomer.valueUint.customerPrice :$t("placeholder.customerdidntbuy") )+ " | " + $t("placeholder.AvarageBuyPrice")+ ':' + UintAndPriceByCUstomer.valueUint.avarage_Price_Inv_Count }}
                </p>
              </div>
            </div>

            <div class="d-flex justify--center mt-3">
              <va-button type="button" color="gray" flat @click="editModalProductPrice? cancleEditfunProductPrice() : cancleCreatefunProductPrice()">
                {{ $t('modal.cancel') }}        </va-button>
              <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t(modelOktextProductprice) }}</va-button>
            </div>
          </form>

        </va-modal>
        <va-modal v-model="deleteModal" no-outside-dismiss
          :title=" $t('generic.deleteItemTitle') "
          :okText=" $t('modal.delete') "
          :cancelText=" $t('modal.cancel') "
          :message=" $t('generic.deleteItemMsg') "
          @ok="okDeletefun()"
          @cancel="cancleDeletefun()">
        </va-modal>

        <va-modal v-model="submitModdal" no-outside-dismiss
          size="large"
          :title="$t(modelTitleProductprice)"
          hide-default-actions >
          <div class="row" >
            <div class="flex md4 xs12">
              <p class="display-5">{{$t('generic.totalBilWithoutTax')}}</p>
              <p>
                {{totalBilWithoutTax}}
              </p>
            </div>
            <div class="flex md4 xs12">
              <p class="display-5">{{$t('generic.totalTax')}}</p>
              <p>
                {{totalTax}}
              </p>
            </div>
            <div class="flex md4 xs12">
              <p class="display-5">{{$t('generic.totalBilTax')}}</p>
              <p>
                {{totalBilTax}}

              </p>
            </div>

          </div>
          <form @submit.prevent="okSubmitfun()">
            <div class="row flex-center">

              <div class="flex md6 sm12 xs12 px-1">
                <va-date-picker
                  :label="$t('placeholder.dueDate')"
                  v-model="ApiRequest.inv_dueDate"
                  required
                />
              </div>
              <div class="flex md6 sm12 xs12 px-1">
                <label class="input-label"> {{$t('placeholder.notes')}} </label>
                <va-input v-model="ApiRequest.notes "
                  :placeholder="$t('placeholder.notes ')" />
              </div>
            </div>

            <div class="d-flex justify--center mt-3">
              <va-button type="button" color="gray" flat @click="canclesubmitfun()">
                {{ $t('modal.cancel') }}        </va-button>
              <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t(modelOktextProductprice) }}</va-button>
            </div>
          </form>

        </va-modal>

        <va-modal v-model="countDownModdal" no-outside-dismiss
          hide-default-actions >
          <div class="row flex-center text-center" style="width: 50vw;">
            <div class="flex md4 xs12">
              <p class="display-5">{{$t('generic.invID')}}</p>
              <p>
                {{invId}}
              </p>
            </div>
          </div>
          <div class="row flex-center pt-5 text-center" >
            <div class="flex md4 xs12">
              <p>
                {{countDown + $t('generic.seconed')}}
              </p>
            </div>
          </div>
        </va-modal>
      </div>
      <div class="flex xs12 md6 ">
        <va-card :title="$t('tables.labelCustomerData')"  class="customerData-infinite-scroll--container">
          <div class="row customerData-infinite-scroll--container" >
            <div class="flex md6 xs6">
              <p class="display-5">{{$t('generic.currentBalance')}}</p>
              <p>
                {{dataTable.user.currentBalance}}
              </p>
            </div>
            <div class="flex md6 xs6">
              <p class="display-5">{{$t('generic.maxDepit')}}</p>
              <p>
                {{dataTable.user.maxDepit }}

              </p>
            </div>
            <div class="flex md6 xs6">
              <p class="display-5">{{$t('generic.is_vip')}}</p>
              <p>
                {{dataTable.user.is_vip ? '&#10004;': '&#10008;'}}

              </p>
            </div>
            <div class="flex md6 xs6">
              <p class="display-5">{{$t('generic.notes')}}</p>
              <p>
                {{dataTable.user.notes}}
              </p>
            </div>
          </div>
        </va-card>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import * as spinners from 'epic-spinners'
import BillService from '../../../../services/BIll/BillService'
import { debounce } from 'lodash'
import helper from '../../../../helper/helper'

export default {
  components: {
    ...spinners,
  },
  props: {
    CustomerId: Number,
  },
  data () {
    return {
      searchNameTerm: null,
      searchBarCodeTerm: null,
      stopBtn: false,
      dataTable: [],
      loading: true,
      showGroupProduct: false,
      groupProduct: [],
      groupsLevels: [],
      modelTitleProductprice: '',
      modelOktextProductprice: '',
      createModalProductPrice: false,
      editModalProductPrice: false,
      deleteModal: false,
      submitModdal: false,
      countDownModdal: false,
      UintAndPriceByCUstomer: {
        valueUint: '',
        valuePrice: 0,
        valueQuantaty: 1,
        currentproduct: '',
        options: [],
      },
      opjectBeforeEdit: {},
      cuurentIndex: '',
      currentobject: {},
      ApiRequest: {
        inv_Subs: [],
      },
      totalBilWithoutTax: 0,
      totalBilTax: 0,
      totalTax: 0,
      tableItems: [],
      countDown: 3,
      invId: 0,
    }
  },
  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
      }, {
        name: 'fullName',
        title: this.$t('tables.headings.name'),
      }, {
        name: 'quantity',
        title: this.$t('tables.headings.quantity'),
      }, {
        name: 'price',
        title: this.$t('tables.headings.price'),
      }, {
        name: 'fullPrice',
        title: this.$t('tables.headings.fullPrice'),
      }, {
        name: 'tax',
        title: this.$t('tables.headings.tax'),
      }, {
        name: 'priceAfterTax',
        title: this.$t('tables.headings.priceAfterTax'),
      }, {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
  },
  watch: {
    'UintAndPriceByCUstomer.valueUint': function (val) {
      if (this.UintAndPriceByCUstomer.valueUint !== val) {
        this.UintAndPriceByCUstomer.valuePrice = val.price
        this.UintAndPriceByCUstomer.valueQuantaty = val.min_Quantity
      }
    },
    tableItems: function (val) {
      this.totalBilWithoutTax = 0.0
      this.totalBilTax = 0.0
      this.totalTax = 0.0
      this.tableItems.forEach(element => {
        this.totalBilWithoutTax = parseFloat(this.totalBilWithoutTax + element.fullPrice)
        this.totalBilTax = parseFloat(this.totalBilTax + element.priceAfterTax)
        this.totalTax = parseFloat(this.totalTax + element.tax)
      })
    },
    searchNameTerm: function (val) {
      if (!this.searchNameTerm || this.searchNameTerm.length < 1) {
        this.showGroupProduct = false
        return this.groupProduct
      } else {
        this.showGroupProduct = true
        this.groupProduct = this.dataTable.allProducts.filter(item => {
          return item.nameAr.toLowerCase().includes(this.searchNameTerm.toLowerCase())
        })
      }
    },
    searchBarCodeTerm: function (val) {
      if (!this.searchBarCodeTerm || this.searchBarCodeTerm.length < 1) {
        this.showGroupProduct = false
        return this.groupProduct
      } else {
        this.showGroupProduct = true

        this.groupProduct = this.dataTable.allProducts.filter(item => {
          if (item.shortCutCode !== null && item.shortCutCode !== undefined) {
            return item.shortCutCode.toLowerCase().includes(this.searchBarCodeTerm.toLowerCase())
          } else {
            return false
          }
        })
      }
    },
  },
  created () {
    this.getnew()
  },
  methods: {
    computedStyle (AvVStock, MinStock) {
      if (AvVStock - 10 > MinStock) {
        return {
          backgroundColor: this.$themes.primary,
        }
      } else if (AvVStock > MinStock) {
        return {
          backgroundColor: this.$themes.gray,
        }
      } else {
        return {
          backgroundColor: this.$themes.danger,
        }
      }
    },
    floatingbtncomputedStyle () {
      if (Vue.i18n.locale() === 'eg' || Vue.i18n.locale() === 'ir') {
        return {
          position: 'fixed',
          width: '60px',
          height: '60px',
          bottom: '23px',
          left: '23px',
          'background-color': '#0C9',
          color: '#FFF',
          'border-radius': '50px',
          'text-align': 'center',
          'box-shadow': '2px 2px 3px #999',
          'z-index': '100',
        }
      } else {
        return {
          position: 'fixed',
          width: '60px',
          height: '60px',
          bottom: '23px',
          right: '23px',
          'background-color': '#0C9',
          color: '#FFF',
          'border-radius': '50px',
          'text-align': 'center',
          'box-shadow': '2px 2px 3px #999',
          'z-index': '100',
        }
      }
    },
    riquierdvalidation (data) {
      return helper.riquierdvalidation(data)
    },
    search: debounce(function (searchNameTerm) {
      this.searchNameTerm = searchNameTerm
    }, 500),
    searchBarcode: debounce(function (searchBarCodeTerm) {
      this.searchBarCodeTerm = searchBarCodeTerm
    }, 500),
    numircriquierdvalidation (d) {
      return helper.numircriquierdvalidation(d)
    },
    numircriquierdvalidationMin (data, min) {
      if (min) {
        if (data >= min) {
          return false
        } else {
          return true
        }
      } else {
        return false
      }
    },
    numircriquierdvalidationMax (data, max) {
      if (max) {
        if (data <= max) {
          return false
        } else {
          return true
        }
      } else {
        return false
      }
    },
    getnew () {
      BillService.getNewBill(this.CustomerId, 1)
        .then(response => {
          console.log(response.data)
          this.dataTable = response.data
          this.groupsLevels.push(response.data.groups)
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    getProductByGroupId (group, rootLevel) {
      this.groupProduct = this.dataTable.allProducts.filter(item => {
        return (item.products_Groups !== null && item.products_Groups.id === group.id)
      })
      if (group.parentGroups.length > 0) {
        this.groupsLevels.splice(rootLevel + 1)
        this.groupsLevels.push(group.parentGroups)
      } else if (this.groupsLevels.length >= rootLevel) {
        this.groupsLevels.splice(rootLevel + 1)
      }
      console.log('product', this.groupProduct)
      console.log('this.nextLevelGroup', this.groupsLevels)
      this.showGroupProduct = true
      return this.groupProduct
    },
    ShowBestSellProductAgain () {
      this.showGroupProduct = false
    },
    addProduct (product) {
      this.modelTitleProductprice = 'generic.creatItemTitle'
      this.modelOktextProductprice = 'modal.create'
      console.log(product)
      BillService.getUintAndPriceByCUstomer(product.id, this.CustomerId)
        .then(response => {
          console.log('getUintAndPriceByCUstomer', response.data)
          this.UintAndPriceByCUstomer.options = response.data
          if (product.fK_DeafultUnits !== null) {
            var defultUintPrice = this.UintAndPriceByCUstomer.options.find(x => x.fk_Uint === product.fK_DeafultUnits)
            if (defultUintPrice !== null && defultUintPrice !== undefined) {
              this.UintAndPriceByCUstomer.valueUint = defultUintPrice
              this.UintAndPriceByCUstomer.valuePrice = defultUintPrice.price
              this.UintAndPriceByCUstomer.valueQuantaty = defultUintPrice.min_Quantity
            } else {
              this.UintAndPriceByCUstomer.valueUint = this.UintAndPriceByCUstomer.options[0]
              this.UintAndPriceByCUstomer.valuePrice = this.UintAndPriceByCUstomer.options[0].price
              this.UintAndPriceByCUstomer.valueQuantaty = this.UintAndPriceByCUstomer.options[0].min_Quantity
            }
          } else {

          }
          this.UintAndPriceByCUstomer.currentproduct = product
          this.createModalProductPrice = true
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    okCreatefunProductPrice () {
      this.stopBtn = true
      console.log(this.UintAndPriceByCUstomer)
      var newItem = {
        fullName: this.UintAndPriceByCUstomer.currentproduct.nameAr,
        quantity: this.UintAndPriceByCUstomer.valueQuantaty + '/' + this.UintAndPriceByCUstomer.valueUint.uint_Name,
        price: this.UintAndPriceByCUstomer.valuePrice,
        fullPrice: (this.UintAndPriceByCUstomer.valuePrice * this.UintAndPriceByCUstomer.valueQuantaty),
        productToEdite: this.UintAndPriceByCUstomer,
        qu: this.UintAndPriceByCUstomer.valueQuantaty,
        productid: this.UintAndPriceByCUstomer.currentproduct.id,
        product: this.UintAndPriceByCUstomer.currentproduct,
        uintid: this.UintAndPriceByCUstomer.valueUint.fk_Uint,
        tax: ((this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100) * (this.UintAndPriceByCUstomer.valuePrice * this.UintAndPriceByCUstomer.valueQuantaty)).toFixed(2),
        taxValue: (this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100),
        priceAfterTax: (this.UintAndPriceByCUstomer.currentproduct.taxGroup.valueHaveTax ? (this.UintAndPriceByCUstomer.valuePrice * this.UintAndPriceByCUstomer.valueQuantaty) : (this.UintAndPriceByCUstomer.valuePrice * this.UintAndPriceByCUstomer.valueQuantaty) + (((this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100) === 0 ? 1 : (this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100)) * (this.UintAndPriceByCUstomer.valuePrice * this.UintAndPriceByCUstomer.valueQuantaty))).toFixed(2),
        stockid: this.UintAndPriceByCUstomer.currentproduct.stock.id,
      }
      if (this.UintAndPriceByCUstomer.currentproduct.taxGroup.valueHaveTax) {
        newItem.fullPrice = (newItem.fullPrice - newItem.tax).toFixed(2)
      }
      var getindex = this.tableItems.findIndex(x => x.productid === this.UintAndPriceByCUstomer.currentproduct.id && x.uintid === this.UintAndPriceByCUstomer.valueUint.fk_Uint)
      if (getindex !== -1) {
        var oldObject = this.tableItems[getindex]
        var newObject = newItem
        newObject.quantity = (newItem.qu + oldObject.qu) + '/' + oldObject.productToEdite.valueUint.uint_Name
        newObject.qu = newItem.qu + oldObject.qu
        newObject.fullPrice = newItem.price * (newItem.qu + oldObject.qu)
        newObject.productToEdite.valueQuantaty = newObject.productToEdite.valueQuantaty + oldObject.productToEdite.valueQuantaty
        newObject.priceAfterTax = oldObject.priceAfterTax + newItem.priceAfterTax
        this.tableItems.splice(getindex, 1, newObject)
      } else {
        this.tableItems.push(newItem)
      }

      this.createModalProductPrice = false
      this.UintAndPriceByCUstomer.valueUint = ''
      this.UintAndPriceByCUstomer.currentproduct = ''
      this.UintAndPriceByCUstomer.valuePrice = 0
      this.UintAndPriceByCUstomer.valueQuantaty = 1
      this.stopBtn = false
    },
    cancleCreatefunProductPrice () {
      this.createModalProductPrice = false
      this.UintAndPriceByCUstomer.valueUint = ''
      this.UintAndPriceByCUstomer.valuePrice = 0
      this.UintAndPriceByCUstomer.valueQuantaty = 1
    },
    /* Delete Model */
    showDeleteModal (id) {
      this.deleteModal = true
      this.cuurentIndex = id
    },
    okDeletefun () {
      this.tableItems.splice(this.cuurentIndex, 1)
    },
    cancleDeletefun () {
      this.deleteModal = false
    },
    /* End Delete Model */

    /* edit model */
    showEditModal (cuobject) {
      this.editModalProductPrice = true
      this.modelTitle = 'generic.editItemTitle'
      this.modelOktext = 'modal.edit'
      this.createModalProductPrice = true
      this.opjectBeforeEdit = Object.assign({}, cuobject)
      this.UintAndPriceByCUstomer.valueUint = cuobject.productToEdite.options.find(el => el.fk_Uint === cuobject.uintid)
      this.UintAndPriceByCUstomer.valuePrice = cuobject.price
      this.UintAndPriceByCUstomer.valueQuantaty = cuobject.qu
      this.UintAndPriceByCUstomer.currentproduct = cuobject.product
    },
    okEditfunProductPrice () {
      this.stopBtn = true
      var newItem = {
        fullName: this.UintAndPriceByCUstomer.currentproduct.nameAr,
        quantity: this.UintAndPriceByCUstomer.valueQuantaty + '/' + this.UintAndPriceByCUstomer.valueUint.uint_Name,
        price: this.UintAndPriceByCUstomer.valuePrice,
        fullPrice: (this.UintAndPriceByCUstomer.valuePrice * this.UintAndPriceByCUstomer.valueQuantaty),
        productToEdite: this.UintAndPriceByCUstomer,
        qu: this.UintAndPriceByCUstomer.valueQuantaty,
        productid: this.UintAndPriceByCUstomer.currentproduct.id,
        product: this.UintAndPriceByCUstomer.currentproduct,
        uintid: this.UintAndPriceByCUstomer.valueUint.fk_Uint,
        tax: (((this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100) === 0 ? 1 : (this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100)) * (this.UintAndPriceByCUstomer.valuePrice * this.UintAndPriceByCUstomer.valueQuantaty)).toFixed(2),
        priceAfterTax: (this.UintAndPriceByCUstomer.currentproduct.taxGroup.valueHaveTax ? (this.UintAndPriceByCUstomer.valuePrice * this.UintAndPriceByCUstomer.valueQuantaty) : (this.UintAndPriceByCUstomer.valuePrice * this.UintAndPriceByCUstomer.valueQuantaty) + ((this.UintAndPriceByCUstomer.currentproduct.taxGroup.value / 100) * (this.UintAndPriceByCUstomer.valuePrice * this.UintAndPriceByCUstomer.valueQuantaty))).toFixed(2),
      }
      var getindex = this.tableItems.findIndex(x => x.productid === this.UintAndPriceByCUstomer.currentproduct.id && x.uintid === this.UintAndPriceByCUstomer.valueUint.fk_Uint)
      if (getindex !== -1) {
        this.tableItems.splice(getindex, 1, newItem)
      } else {
        this.tableItems.push(newItem)
      }
      this.editModalProductPrice = false
      this.createModalProductPrice = false
      this.stopBtn = false
      this.UintAndPriceByCUstomer.valueUint = ''
      this.UintAndPriceByCUstomer.currentproduct = ''
      this.UintAndPriceByCUstomer.valuePrice = 0
      this.UintAndPriceByCUstomer.valueQuantaty = 1
    },
    cancleEditfunProductPrice () {
      this.editModalProductPrice = false
      Object.assign(this.currentobject, this.opjectBeforeEdit)
      this.currentobject = {}
      this.createModalProductPrice = false
      this.UintAndPriceByCUstomer.valueUint = ''
      this.UintAndPriceByCUstomer.currentproduct = ''
      this.UintAndPriceByCUstomer.valuePrice = 0
      this.UintAndPriceByCUstomer.valueQuantaty = 1
    },
    /* end edit model */

    /* finish  */
    showsubmitModdal () {
      if (this.tableItems.length >= 1) {
        this.modelTitle = 'generic.finishBill'
        this.modelOktext = 'modal.ok'
        this.submitModdal = true
        this.ApiRequest.inv_dueDate = helper.todayDate()
      } else {
        this.showToast(this.$t('generic.emptyBil'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
    },
    okSubmitfun () {
      this.stopBtn = true
      this.ApiRequest.total_Price = this.totalBilTax
      this.ApiRequest.total_Tax = this.totalTax
      this.ApiRequest.is_Cash = false
      this.ApiRequest.liveStatus = 'order'
      this.ApiRequest.date = helper.todayDate()
      this.ApiRequest.fK_Branchs = 2
      this.ApiRequest.fK_Customers = this.CustomerId
      this.ApiRequest.fK_Inv_type = 2
      this.ApiRequest.fK_Safes = 1
      this.ApiRequest.fK_Inv_PayType = 1
      this.ApiRequest.fK_Pay_Type = 1
      this.ApiRequest.fK_Employees = 1
      this.tableItems.forEach(element => {
        var newSubs = {
          quantity: element.qu,
          price: element.price,
          tax: element.taxValue * 100,
          fK_Products: element.productid,
          fK_Stock: element.stockid,
          fK_Units: element.uintid,
        }
        console.log(element)
        this.ApiRequest.inv_Subs.push(newSubs)
      })
      console.log(this.ApiRequest)
      BillService.create(this.ApiRequest).then(res => {
        this.ApiRequest = {
          inv_Subs: [],
        }
        this.submitModdal = false
        this.countDownModdal = true
        this.invId = res.data.inv_id !== null ? res.data.inv_id : '----'
        setInterval(() => {
          this.countDown -= 1
        }, 1000)
        setTimeout(() => {
          location.reload()
        }, 3000)
      },
      err => {
        console.error(err)
        this.loading = false
        this.stopBtn = false
        this.submitModdal = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },
    canclesubmitfun () {
      this.submitModdal = false
      this.ApiRequest = {}
    },
    /* end finish */

  },
}

</script>

