import http from '../../http-common'

class CustomerslistService {
  getAll (params) {
    return http.get('Customers', { params })
  }

  getAllPagnation (params) {
    return http.get('CustomersPaging', { params })
  }

  get (id) {
    return http.get(`Customers/getById?Id=${id}`)
  }

  search (params) {
    return http.get('Customers/Search', { params })
  }

  create (data) {
    return http.post('Customers', data)
  }

  update (id, data) {
    return http.put(`Customers?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Customers?Id=${id}`)
  }
}

export default new CustomerslistService()
